:root {
  font-size: 62.5%;
}

*, :before, :after {
  box-sizing: border-box;
  font: inherit;
  margin: 0;
  padding: 0;
}

img, picture, svg, video {
  max-width: 100%;
  display: block;
}

body {
  min-height: 95vh;
  background-color: #fdfdfd;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.header {
  height: 10rem;
  width: 100%;
  opacity: .86;
  z-index: 100;
  background-color: #fdfdfd;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  display: flex;
  position: fixed;
  top: 0;
}

@media screen and (min-width: 501px) {
  .header {
    height: 6rem;
    flex-direction: row;
    padding-top: 1rem;
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

@media screen and (min-width: 640px) {
  .header {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

.navbar ul {
  width: -moz-fit-content;
  width: fit-content;
  align-items: center;
  font-size: 1.8rem;
  font-weight: 500;
  list-style-type: none;
  display: flex;
}

.navbar a {
  color: #000;
  letter-spacing: 1px;
  border-radius: 2rem;
  margin: 0 1rem;
  padding: .4rem .9rem;
  text-decoration: none;
}

.navbar a:hover {
  color: #fdfdfd;
  background-color: #000;
  border-radius: 2rem;
  transition-duration: .5s;
}

.navbar a:active {
  text-decoration: underline;
}

.logo {
  height: 5rem;
  width: 10rem;
  background-image: url("logo05.6d9f5814.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

main {
  width: 80%;
  height: 80vh;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: relative;
  top: 12rem;
}

@media screen and (min-width: 501px) {
  main {
    top: 8rem;
  }
}

@media screen and (min-width: 1200px) {
  main {
    width: 80%;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.avatar {
  height: 35vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.avatar-pic {
  min-width: 20rem;
  min-height: 20rem;
  max-width: 20rem;
  max-height: 20rem;
  background-image: url("webavatar.ea27bc21.png");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
}

h1 {
  text-align: center;
  margin-top: 2rem;
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 2.4rem;
}

.about-text {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.about-text p {
  width: 80%;
  text-align: center;
  margin: 0 auto 5rem;
  font-size: 1.6rem;
  line-height: 2.2rem;
}

@media screen and (min-width: 1200px) {
  .about-text p {
    width: 80%;
  }
}

.about-text .about-links {
  list-style-type: none;
  display: flex;
}

.about-text .about-links li a {
  color: #000;
  border: 1px solid #000;
  border-radius: 2rem;
  margin: 0 2rem;
  padding: .4rem .9rem;
  font-size: 1.8rem;
  text-decoration: none;
}

.about-text .about-links li a:hover {
  color: #fdfdfd;
  background-color: #000;
  border-radius: 2rem;
  text-decoration: none;
  transition-duration: .5s;
}

.about-text .about-links li a:active {
  text-decoration: underline;
}

.about-text .about-links li i {
  margin-right: .5rem;
}

